import axios from "axios";

//   get
export const getTodo = async () => {
  try {
    const res = await axios.get("/api/todo");
    const result = res.data;
    return result;
  } catch (error) {
    console.log(error);
    return {
      level: 1,
      icategory: [
        {
          cate_name: "",
          itodo: 13,
          title: "Scenic Route, The",
          bookmark: 25,
          del: 0,
          finish: "",
          start: "2023-10-01",
          end: "2023-10-05",
          color: "#53B0AB",
          isbn: "8278923705078",
        },
      ],
    };
  }
};

